"use client";

import { createContext, useState, useEffect } from "react";
import authService from "../services/authService";

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    checkAuth();
  }, []);

  const checkAuth = async () => {
    const token = localStorage.getItem(process.env.NEXT_PUBLIC_JWT_STORAGE_KEY);
    if (token) {
      try {
        const userData = await authService.getProfile();
        setUser(userData);
        setIsAuthenticated(true);
        setIsAdmin(userData.is_staff || userData.is_superuser || false);
      } catch (error) {
        console.error("Auth check failed:", error);
        // Try refreshing token
        try {
          await authService.refreshToken();
          const refreshedUser = await authService.getProfile();
          setUser(refreshedUser);
          setIsAuthenticated(true);
          setIsAdmin(refreshedUser.is_staff || refreshedUser.is_superuser || false);
        } catch (refreshError) {
          console.error("Token refresh failed:", refreshError);
          logout();
        }
      }
    }
    setLoading(false);
  };

  const login = async (email, password) => {
    try {
      const res = await authService.login(email, password);
      const { token, refresh } = res;

      localStorage.setItem(process.env.NEXT_PUBLIC_JWT_STORAGE_KEY, token);
      localStorage.setItem(
        process.env.NEXT_PUBLIC_JWT_REFRESH_KEY || "refresh_token",
        refresh
      );

      await checkAuth();
      return { success: true };
    } catch (err) {
      console.error("Login error:", err);
      return {
        success: false,
        message: err.detail || err.message || "Login failed",
      };
    }
  };

  const signup = async (userData) => {
    try {
      await authService.signup(userData);
      return { success: true };
    } catch (err) {
      console.error("Signup error:", err);

      let errorMessage = "Signup failed";
      if (typeof err === "object") {
        const firstError = Object.values(err)[0];
        if (Array.isArray(firstError)) {
          errorMessage = firstError[0];
        } else if (typeof firstError === "string") {
          errorMessage = firstError;
        }
      } else if (err.message) {
        errorMessage = err.message;
      }

      return { success: false, message: errorMessage };
    }
  };

  const logout = () => {
    authService.logout();
    localStorage.removeItem(process.env.NEXT_PUBLIC_JWT_STORAGE_KEY);
    localStorage.removeItem(process.env.NEXT_PUBLIC_JWT_REFRESH_KEY || "refresh_token");
    setUser(null);
    setIsAuthenticated(false);
    setIsAdmin(false);
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        isAuthenticated,
        isAdmin,
        loading,
        login,
        signup,
        logout,
        checkAuth,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
